footer {
    background: var(--color-primary);
    padding: 3rem 0;
    text-align: center;
    font-size: 0.9rem;
    margin-top: 7rem;
    border-radius: 1.2rem 1.2rem 0 0;
}

footer a {
    color: var(--color-bg);
}
.footer_logo {
    font-size: 2rem;
    font-weight: 500;
    margin-bottom: 2rem;
    display: inline-block;
}
.permalinks {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    gap: 2rem;
    margin: 0 auto 3rem;
}
.footer_socials {

    display: flex;
    justify-content: center;
    gap: 1rem;
    margin-bottom: 4rem;
}

.footer_socials a {
    background: var(--color-bg);
    color: var(--color-white);
    padding: 0.8rem;
    border-radius: 0.7rem;
    display: flex;
    border: 1px solid transparent;
}
.footer_socials a:hover {
    border-color: var(--color-bg);
    color: var(--color-bg);
    background: transparent;
}
.footer_copyright {
    margin-bottom: 4rem;
    color: var(--color-bg);
}
/* ========== mobile======== */
@media screen and (max-width:600px) {
    .permalinks {
        flex-direction: column;
        gap: 1.5em;
    }
    .footer_socials{
        margin-bottom: 2.6rem;

    }
}